import 'rapidoc'
import './index.css'

import React from 'react'
import { PaddedContent, PageHeader, theme } from '@aserto/console-common'

type ApiBrowserProps = {
  pageTitle: string
  openapiUrl: string
}
const ApiBrowser: React.FC<ApiBrowserProps> = ({ pageTitle, openapiUrl }) => {
  return (
    <>
      <PageHeader hasBorderBottom title={pageTitle} />
      <PaddedContent>
        <rapi-doc
          bg-color={theme.primaryBlack}
          nav-bg-color={theme.grey10}
          primary-color={theme.primary}
          render-style="read"
          show-header="false"
          spec-url={openapiUrl}
          style={{ height: 'calc(100vh - 140px)', width: '100%' }}
        ></rapi-doc>
      </PaddedContent>
    </>
  )
}

export default ApiBrowser
